<template>
    <div>首页{{showText}}</div>
    <div>2{{showText2}}</div>
    <input v-model="showText" />
    <div>数组长度{{arrlength}}</div>
    <ul>
        <li v-for="item,index in list" :key="index">{{item.name}}</li>
    </ul>
    <button @click="add">添加</button>
    <button @click="toDetail">跳转</button>
</template>
<script>
import { computed, defineComponent, reactive, ref, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'

export default defineComponent({
    name: 'index-page',
    setup() {
        const router = useRouter()
        const toDetail = () => {
            router.push('/baseform')
        }
        const showText = ref('你好i')
        showText.value = '22'
        const showText2 = reactive({
            name: '12'
        })
        const list = reactive([
            {
                name: 1
            }
        ])
        const add = () => {
            if(showText.value) {
                list.push({
                    name: showText.value
                })
                showText.value = ''
            }
        }
        const route = useRoute()
        watch(list, (val) => {
            console.log(val)
            console.log(val[0].name)
        })
        watch(
            () => route.path,
            (val) => {
                console.log(val)
            }
        )
        const arrlength = computed(() => list.length)
        showText2.name = 999
        return {
            toDetail,
            showText,
            showText2,
            list,
            add,
            arrlength
        }
    },
})
</script>
